// EditProfileForm.jsx
import React, { useState, useEffect } from 'react';

function EditProfileForm({ initialData, onSave, onCancel }) {
  const [formData, setFormData] = useState(initialData);
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [passwordError, setPasswordError] = useState('');

  // Sincroniza el estado interno del formulario si initialData cambia desde el padre
  useEffect(() => {
    setFormData(initialData);
    // Limpiar campos de contrasea al recargar o cancelar
    setNewPassword('');
    setConfirmPassword('');
    setPasswordError('');
  }, [initialData]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handlePasswordChange = (e) => {
    setNewPassword(e.target.value);
    // Limpiar el error cuando el usuario empieza a escribir
    if (passwordError) setPasswordError('');
  };

  const handleConfirmPasswordChange = (e) => {
    setConfirmPassword(e.target.value);
    // Limpiar el error cuando el usuario empieza a escribir
    if (passwordError) setPasswordError('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Validar contraseas si se est intentando cambiar
    if (newPassword || confirmPassword) { // Solo validar si alguno de los campos de contrasea no est vaco
      if (newPassword.length < 6) { // Ejemplo de validacin de longitud mnima
        setPasswordError('La contrasea debe tener al menos 6 caracteres.');
        return;
      }
      if (newPassword !== confirmPassword) {
        setPasswordError('Las contraseas no coinciden.');
        return;
      }
    }

    // Si todo es vlido o no se est cambiando la contrasea, procede
    onSave(formData, newPassword); // Pasa tambin la nueva contrasea (vaca si no se cambi)
    setNewPassword(''); // Limpia los campos despus de guardar
    setConfirmPassword('');
  };

  return (
    <form onSubmit={handleSubmit} className="edit-profile-form">
      <h2>Editar Perfil</h2>
      <div className="form-group">
        <label htmlFor="name">Nombre:</label>
        <input
          type="text"
          id="name"
          name="name"
          value={formData.name}
          onChange={handleChange}
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="username">Nombre de Usuario:</label>
        <input
          type="text"
          id="username"
          name="username"
          value={formData.username}
          onChange={handleChange}
          required
        />
      </div>

      <h3 className="section-subtitle">Cambiar Contrasea (opcional)</h3>
      <div className="form-group">
        <label htmlFor="newPassword">Nueva Contrasea:</label>
        <input
          type="password"
          id="newPassword"
          name="newPassword"
          value={newPassword}
          onChange={handlePasswordChange}
          placeholder="Dejar en blanco para no cambiar"
        />
      </div>
      <div className="form-group">
        <label htmlFor="confirmPassword">Confirmar Contrasea:</label>
        <input
          type="password"
          id="confirmPassword"
          name="confirmPassword"
          value={confirmPassword}
          onChange={handleConfirmPasswordChange}
        />
      </div>
      {passwordError && <p className="error-message" style={{ color: 'red', fontSize: '0.9em', marginTop: '-0.5rem', marginBottom: '1rem' }}>{passwordError}</p>}

      <div className="form-actions">
        <button type="submit" className="btn-primary">Guardar Cambios</button>
        <button type="button" className="btn-secondary" onClick={onCancel}>Cancelar</button>
      </div>
    </form>
  );
}

export default EditProfileForm;
